export default {
	data() {
		return {
			list: [],
			createActive: false,
			inputVal: '',
			tabActive:0,
			statusTxt: '全部',
			inputShow: false
		}
	},
	onLoad() {

	},
	methods: {
		showInput() {
			if(this.createActive) {
				this.closeInput()
			}else{
				this.openInput()
			}
		},
		openInput(){
			this.createActive = true;
			this.$nextTick(()=>{
				setTimeout(()=>{
					this.inputShow = true
				},50)
			})
		},
		closeInput(){
			this.inputShow = false
			this.$nextTick(()=>{
				setTimeout(()=>{
					this.createActive = false
				},50)
			})
		},
		addNew() {
			if(this.inputVal === '') {
				uni.showToast({
					title: '请输入内容',
					icon: 'none'
				})
				return
			}
			this.list.unshift({
				id: 'id' + new Date().getTime(),
				content: this.inputVal,
				checked: false
			})
			this.inputVal = ''
			this.closeInput()
		},
		toggleFinish(id) {
			let index = this.list.findIndex((item)=>item.id === id)
			this.list[index].checked = !this.list[index].checked;
		},
		tab: function(t) {
			this.tabActive = t;
		}
	},
	computed: {
		listData: function(){
			let list = JSON.parse(JSON.stringify(this.list));
			let newList = [];
			if(this.tabActive === 0) {
				this.statusTxt = '全部';
				return list
			}
			if(this.tabActive === 1) {
				this.statusTxt = '代办';
				list.forEach((item)=>{
					if(!item.checked) {
						newList.push(item) 
					}
				})
				return newList
			}
			if(this.tabActive === 2) {
				this.statusTxt = '已完成';
				list.forEach((item)=>{
					if(item.checked) {
						newList.push(item) 
					}
				})
				return newList
			}
		}
	}
}
